@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
body{margin:0;font-size:16px;font-family:"Poppins", sans-serif}.container{width:100vw;height:100vh;background:url(/card-bowl/static/media/bg.7e6ac3f7.png)}.App{padding:0 12vw;font-family:"montserrat";font-size:16px;color:#065c6f;background-color:#065c6f}.App .title{font-size:2.7rem;color:#065c6f;font-weight:700}.App .title span{font-weight:300;font-size:1.5rem}.App .form{display:flex;justify-content:center}.App .form label{margin-right:1.5rem}.App .form label input{margin-left:1rem;border-radius:10px;outline:none;padding:5px 0.9rem;border-color:#065c6f;border-width:1px}.App .form .btn-submit{border-radius:30px;color:#ffffff;background-color:#065c6f;border:none;padding:0 1.2rem}.App .form .btn-submit:hover{background-color:#13697c;cursor:pointer}.App .card-style{width:240px;border-radius:20px;overflow:hidden;margin-bottom:1.5rem;transition:transform 0.5 ease}.App .card-style:hover{transform:scale(1.1);transition:transform 0.3s ease}.App .card-style .button-wrapper{display:flex;justify-content:space-between}.App .card-style .button-wrapper .button-del{background-color:#df2f63;border-radius:5px;padding:4px 0.7rem;color:#ffffff}.App .card-style .button-wrapper .button-del:hover{background-color:#f04e7e}.App .card-style .button-wrapper .button-upd{background-color:#2f84df;border-radius:5px;padding:4px 0.7rem;color:#ffffff}.App .card-style .button-wrapper .button-upd:hover{background-color:#499ef8}.App .import-section{display:flex;width:35%;justify-content:space-between;margin:2rem auto}.App .import-section .btn-import{cursor:pointer;font-size:1rem;background-color:#13697c;color:#ffffff;font-weight:normal;border-radius:10px;outline:none;border:none;padding:0.4rem 1.2rem}.App .import-section .btn-import:hover{background-color:#197286}.App .import-section .btn-import span{font-weight:bold}@media (max-width: 1175px){.App .import-section .btn-import{font-size:0.8rem;padding:0.4 0.8rem}}@media (max-width: 1000px){.App .import-section{width:50vw}}

